.search {
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  text-align: center;

  padding: 38px 18px 49px;
}

.search__wrapper {
  width: 261px;
  height: 110px;
  margin: 0 auto;
  margin-bottom: 32px;
}

.search__title {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  color: #000000;
  margin: 0;
  margin-bottom: 31px;
}

.search__description {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  margin: 0;
}


.search__find-hotel {
  display: inline-block;
  width: 186px;
  padding: 16px 49px;
  background-color: #81b3d2;
}

@media (min-width: @tablet-width) {
  .search {
    padding: 61px 155px 58px;
  }

  .search__wrapper {
    width: 435px;
    height: 100px;
  }

  .search__title {
    font-size: 30px;
    margin-bottom: 39px;
  }

  .search__description {
    font-size: 14px;
    line-height: 26px;
  }

  .search__find-hotel {
    width: 216px;
    padding: 16px 121px;
  }
}


@media (min-width: @desktop-width) {
  .search {
    padding: 61px 371px 66px;
  }

  .search__find-hotel {
    padding: 14.5px 121px 17.5px;
  }
}
